<template>
  <Carousel ref="carousel" :wrapAround="true">
    <Slide v-for="(item, index) of actions" :key="index">
      <div class="action">
        <img :src="getImgUrl(item.src)" class="act3" alt="" />
        <div class="action__info">
          <h2 class="act">{{ item.title }}</h2>
          <p>{{ item.description }}</p>
        </div>
      </div>
    </Slide>
    <template #addons>
      <Navigation />
    </template>
  </Carousel>
</template>

<script setup>
import { Carousel, Navigation, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";
import actionItems from "../data/actions.json";
import { ref } from "vue";

const actions = ref(actionItems);

const actionSettings = ref({
  dots: false,
  arrows: false,
  infinite: true,
  slidesToShow: actions.value.length,
  slidesToScroll: actions.value.length,
  adaptiveHeight: true,
});

const getImgUrl = (src) => {
  return require(`../assets/${src}`);
};
</script>

<style lang="scss">
.carousel {
  max-width: 1300px;
  margin: 0 auto;
  @media (max-width: 1350px) {
    width: 85%;
  }
  @media (max-width: 740px) {
    max-width: 580px;
  }
  @media (max-width: 620px) {
    max-width: 100vw;
    width: 100%;
  }

  .carousel__slide {
    padding: 0 50px;
  }
  .action {
    display: flex;
    @media (max-width: 820px) {
      flex-direction: column-reverse;
    }

    .action__info {
      padding: 20px 0 0 20px;
      @media (max-width: 820px) {
          margin-bottom: 20px;
        }

      h2 {
        margin-bottom: 30px;
        @media (max-width: 820px) {
          margin-bottom: 10px;
        }
      }
    }
    img {
      width: 60%;
      @media (max-width: 820px) {
        width: 100%;
        max-height: 350px;
      }
    }
  }
}
</style>
